import { Component, OnInit, ViewChild, AfterViewInit, Sanitizer, Renderer, Renderer2 } from '@angular/core';
import { NgbCarousel, NgbCarouselConfig } from '@ng-bootstrap/ng-bootstrap';
import { NgbSlideEvent } from '@ng-bootstrap/ng-bootstrap/carousel/carousel';
import { ConstantsService } from '../providers/constants.service';
import { DomSanitizer } from '@angular/platform-browser';
import { element } from '@angular/core/src/render3';

@Component({
  selector: 'app-advertising-slide',
  templateUrl: './advertising-slide.component.html',
  styleUrls: ['./advertising-slide.component.scss'],
  providers: [NgbCarouselConfig]
})
export class AdvertisingSlideComponent implements OnInit {
  @ViewChild('carousel') carousel: NgbCarousel;
  showNavigationArrows = false;
  showNavigationIndicators = false;
  timePerSlide: number = 1000;
  constructor(config: NgbCarouselConfig, private constantsService: ConstantsService, private sanitizer: DomSanitizer,
    private renderer: Renderer) {
    // customize default values of carousels used by this component tree
    config.showNavigationArrows = false;
    config.showNavigationIndicators = false;

    renderer.listenGlobal('document', 'touchend', (e) => {
      this.openFullscreen();
    });
    renderer.listenGlobal('document', 'click', (e) => {
      this.openFullscreen();
    });

  }
  elem;
  private slideData;
  ngOnInit() {
    this.slideData = this.constantsService.data;
    console.log('Slide data', this.slideData);
    // this.openFullscreen();
    this.elem = document.documentElement;
  }

  returnSanitizedUrl(url) {
    return this.sanitizer.bypassSecurityTrustResourceUrl(url);
  }
  // tslint:disable-next-line: use-life-cycle-interface
  ngAfterViewInit() {
    // this.carousel.pause();
    this.openFullscreen();
    this.getLocation();
  }

  onSlide(slideEvent: NgbSlideEvent) {
    console.log('TIME LAPSE FOR SLIDE', slideEvent.current);
    const slideIndex = slideEvent.current.split('-');
    this.timePerSlide = this.constantsService.data[slideIndex[slideIndex.length - 1]].time;
  }

  openFullscreen() {
    if (this.elem.requestFullscreen) {
      this.elem.requestFullscreen();
    } else if (this.elem.mozRequestFullScreen) {
      /* Firefox */
      this.elem.mozRequestFullScreen();
    } else if (this.elem.webkitRequestFullscreen) {
      /* Chrome, Safari and Opera */
      this.elem.webkitRequestFullscreen();
    } else if (this.elem.msRequestFullscreen) {
      /* IE/Edge */
      this.elem.msRequestFullscreen();
    }
  }

  getLocation() {
      navigator.geolocation.getCurrentPosition((position) => {
        console.log('Latitude : ' + position.coords.latitude);
        console.log('Longitude : ' + position.coords.longitude);
      });
  }
}
